.main-container {
    font-family: 'Blair ITC';
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-top: 1vw; }

.logo {
    font-size: 3vw;
    letter-spacing: 1vw; }

.subtitle {
    width: 45vw;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.text-container {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-top: 3vw; }

.menu ul {
    font-size: 1.25vw;
    line-height: 3vw;
    padding-left: 1vw;
    letter-spacing: 0.25vw;
    margin: 0; }

.content {
    width: 45vw;
    font-size: 2vw;
    line-height: 2.5vw;
    letter-spacing: 0.05vw;
    height: 85vh;
    overflow-y: scroll; }

.content::-webkit-scrollbar {
    display: none; }


.content p {
    margin-top: 0; }

li a:hover {
    font-style: italic; }

@media only screen and (max-width: 835px) {
    .main-container {
        display: flex;
        flex-direction: column;
        margin-left: 2vh; }

    .logo {
        font-size: 8.5vw;
        letter-spacing: 1.2vw;
        margin-bottom: 1vh;
        margin-top: 1vh; }


    .subtitle {
        font-size: 2vw; }

    .text-container {
        display: block; }

    .menu {
        position: absolute;
        top: 2vh;
        right: 4vh; }

    .menu ul {
        font-size: 3vw;
        line-height: 7vw; }

    .content {
        margin: 15vh 2vh;
        font-size: 4vw;
        width: 85vw;
        line-height: 5vw; } }
